$(function () {
    $('.rating-container input[type="radio"]').on('change', function () {
        if ($(this).is(':checked')) {
            var rateN = parseInt($(this).val()) + 1;
//                    $('#postTemplate label > span.styled-radio').css('background-position', '-34px 0');
            $('#postTemplate label > span.styled-radio').css('background-position', '-26px 0');
            for (i = 1; i < rateN; i++) {
                $('#postTemplate label > span.styled-radio[data-value="' + i + '"]').css('background-position', '0 0');
            }
        }
    });
});